import React from "react";
import './rensyu.css';

class Rensyu extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mondai:[],
            kaitou:[],
            isKaitouOpen:true,
            startTime:Date.now(),
            elapsedTime:0,
            nowTime:"経過時間 00:00",
            start:false
        }
    }
    
setTime = () => {
    let time = Math.floor((Date.now() - this.state.startTime)/1000);
    this.setState({elapsedTime:time});
    let m = `0${Math.floor(this.state.elapsedTime / 60)}`.slice(-2);
    let s = `0${this.state.elapsedTime % 60}`.slice(-2);
    this.setState({nowTime:`経過時間 ${m}:${s}`})
};
start() {
    this.setState({
        startTime:Date.now(),
        start:true
    });
    this.timer = setInterval(() => this.setTime(), 1000);
}
stop() {
    if(this.state.start){
    this.stop = clearInterval(this.timer);
    this.setState({start:false});
    }
}
reset() {
    if(this.state.start){
        this.stop = clearInterval(this.timer);
        this.setState({
            start:false,
            nowTime:"経過時間 00:00"
        }); 
    }else{
        this.setState({
            nowTime:"00:00"
        }); 
    }
}

    mondaiClick() {
        let mondai = []
        const kaitou = []
        for(let i =0; i < 10; i++) {
            const mondai1 = Math.random().toFixed(3)*1000/Math.pow(10,Math.floor(Math.random()*4));
            const mondai2 = Math.random().toFixed(3)*1000/Math.pow(10,Math.floor(Math.random()*4));
            if(i % 2 ===0) {
                const kaitou1 = mondai1 * mondai2;
                const kaitou2 = Math.floor(Math.log10(kaitou1));
                const kaitou3 = `(${i+1}) : ${mondai1} × ${mondai2} = ${Math.round(kaitou1*100000000)/100000000}`;
                const kaitou4 = `<ANS> ${Math.round(kaitou1*100/Math.pow(10,kaitou2))/100}×10^${kaitou2}`
                mondai.push(`(${i+1}) : ${mondai1} × ${mondai2} =`);
                mondai.push(".");
                kaitou.push(kaitou3);
                kaitou.push(kaitou4);
            }else{
                const kaitou1 = mondai1 / mondai2;
                const kaitou2 = Math.floor(Math.log10(kaitou1));
                const kaitou3 = `(${i+1}) : ${mondai1} ÷ ${mondai2} = ${Math.round(kaitou1*100000000)/100000000}`;
                const kaitou4 = `<ANS> ${Math.round(kaitou1*100/Math.pow(10,kaitou2))/100}×10^${kaitou2}`
                mondai.push(`(${i+1}) : ${mondai1} ÷ ${mondai2} =`);
                mondai.push(".");
                kaitou.push(kaitou3);
                kaitou.push(kaitou4);
            }

        }
        this.setState({mondai:mondai});
        this.setState({kaitou:kaitou});
        this.setState({isKaitouOpen:true});
    }

    kaitouClick() {
        this.setState({isKaitouOpen:false});
    }
    

    render() {
        return (
            <div>
                <button className= "mondaibutton" onClick={() => {this.mondaiClick()}}>問題作成</button>
                <button className= "mondaibutton" onClick={() => {this.kaitouClick()}}>解答表示</button> 
                <button className= "timerbuttom" onClick={() => {this.start()}}>Start</button>
                <button className= "timerbuttom" onClick={() => {this.stop()}}>Stop</button> 
                <button className= "timerbuttom" onClick={() => {this.reset()}}>Reset</button>
                <div>{this.state.nowTime}</div>
                {
                    (() => {
                    if (this.state.isKaitouOpen) {
                        return(
                            this.state.mondai.map((mondai) => {
                                return (
                                    <p>{mondai}</p>
                                );
                            }));
                    } else {
                        return (this.state.kaitou.map((mondai) => {
                            return (
                                <p>{mondai}</p>
                            );
                        }));
                    }
                    })()
                }
            </div>
        );
    }
}
export default Rensyu;